  .modal-body {
    padding: 2px 16px;
    padding-top: 2% !important;
    overflow: auto;
    height: 465px;
  }
  .modal-body p{
    color:black !important;
  }
  /* width */
::-webkit-scrollbar {
  width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #bbb7b7; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;  
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;  
}
  
