
.mainDiv{
    padding-top: 0.5%;
    padding-left: 1%;
    padding-right: 1%;
    box-sizing: border-box;
}
.userInfoDiv{
    margin-left: 1%;
    margin-right: 1%;
    height: 260px;
    -webkit-box-shadow: 2px 7px 46px -12px rgba(0,0,0,0.75);
    -moz-box-shadow: 2px 7px 46px -12px rgba(0,0,0,0.75);
    box-shadow: 2px 7px 46px -12px rgba(0,0,0,0.75);
}
.backgroundOfUserInfo{
    background-color: rgb(72, 219, 138);
    height: 95px;
}
.userInfo{
    background-color: white;
    height: 160px;  
}
.userIconHome{
    position: absolute;
    margin-top: -2.7%;
    width: 80px;
    height: 80px;
    border-width: 4px;
    border-color: white;
    cursor: pointer;
}

.userIconHome .md-avatar{
    height: 80px !important;
    width: 80px !important;
    border-width: 4px;
    border-color: white;
    cursor: pointer;
}
.userIconHome .md-avatar-content{
    font-size: 32px;
}
.userIconHome .md-avatar--default{
    background-color: rgb(72, 219, 138);
}
.userNameInfo{
    margin-top: -5%;
}
.userEmailInfo{
    margin-top: -8%;
    font-size: 10px;
}
.alt{
    font-size:12px !important;
}
.shareButton{
    color: white;
}