
.Hovered:hover{
    background: rgba(0,0,0,0.12);
}
.UserIcon{
    position: absolute;
    right:0%;
    top:10px;
}
.ToasterLeftTopSide{
    position: absolute ;
    top: 1%;
}
.LoadingScrean{
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%, -50%);
}
.ToasterLeftTopSide .toast{
    color: white !important;
}
.navBar{
    padding-top:0.2%;
    padding-right: 1%;
}
.UserIconLogo{
    color: white !important;
}
.navbar{
    background-color: white;
    border-bottom: 0.1px solid #d7d8da;
    -webkit-box-shadow: 0px 0px 20px -5px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 20px -5px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 20px -5px rgba(0,0,0,0.75);
}
.md-title--toolbar{
    color: rgb(72, 219, 138) !important;
}
.userPosting{
    background-color: rgb(72, 219, 138);
}
.postings{
    -webkit-box-shadow: 0px 0px 20px -5px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 20px -5px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 20px -5px rgba(0,0,0,0.75);
}
.userPosting .md-avatar{
    height: 50px !important;
    width: 50px !important;
    border-width: 2px;
    border-color: white;
}
.userPosting .userIconHome{
    margin-top: 0% !important;
    position: relative;
}
.wordsBox{
    width: 100%;
}
.wordsBox .md-divider{
    background-color: white;
}
.wordsBox .md-text--secondary{
    padding: 1%;
}
.insideHomeUserIcon{
    padding-top: 50%;
}
.wordsBox .md-text-field-multiline-container{
    border-top: 1px solid white;
    border-right: 1px solid white;
    border-left: 1px solid white;
}