.userPostBox{
    -webkit-transition-timing-function: ease;
    transition-timing-function: ease;
    -webkit-transition: 0.5s; /* Safari */
    transition: 0.5s;
}
.userPostBox:hover{
    margin-left: 1%;
    margin-right: 1%;
    -webkit-box-shadow: 2px 7px 46px -12px rgba(0,0,0,0.75);
    -moz-box-shadow: 2px 7px 46px -12px rgba(0,0,0,0.75);
    box-shadow: 2px 7px 46px -12px rgba(0,0,0,0.75);
}
.userIconPostBox .md-avatar--default{
    color: white;
    background-color: rgb(72, 219, 138);
}
.userMessage{
    max-width:402.250px;
}
.deleteBtn:hover{
    color: rgb(207, 6, 6);
    cursor: pointer;
}

